$body-background: #202b38;
$text-color: #fff;
$text-main-color: #dbdbdb;
$link-color: #41adff;

body {
  font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', 'Segoe UI Emoji', 'Apple Color Emoji', 'Noto Color Emoji', sans-serif;
  line-height: 1.4;
  max-width: 800px;
  margin: 20px auto;
  padding: 0 10px;
  word-wrap: break-word;
  text-rendering: optimizeLegibility;

  color: $text-color;
  background-color: $body-background;

  a {
    color: $link-color;

    h1,
    h2 {
      color: $text-color;
    }
  }

  h1 {
    font-weight: 600;
    font-size: 2.2em;
    margin-bottom: 12px;
    margin-top: 24px;
  }

  a {
    text-decoration: none;
  }

  header {
    margin-bottom: 20px;

    nav {
      ul {
        list-style-type: none;
        margin: 0;
        padding: 0;
        overflow: hidden;

        li {
          display: inline;

          a {
            text-align: center;
            padding-left: 0;
            padding-right: 20px;
            text-decoration: none;
          }
        }
      }
    }
  }

  main {
    img {
      max-width: 100%;
    }

    p {
      color: $text-main-color;
    }
  }

  article {
    border-bottom: solid 1px #526980;
    padding-bottom: 10px;
    margin-bottom: 40px;
  }

  .author {
    padding: 25px 0;
    margin-top: 40px;
    border-top: solid 1px #526980;
    border-bottom: solid 1px #526980;
  }

  nav.pagination {
    display: flex;
    justify-content: space-between;
    padding: 25px 0;
    border-top: solid 1px #526980;
  }

  footer {
    text-align: center;
    padding: 25px 0;
    border-top: solid 1px #526980;
  }
}

.comment-respond {
  .comment-form-comment {
    label {
      display: block;
    }

    textarea {
      width: 100%;
    }
  }

  .form-submit {
    input[type="submit"] {
      cursor: pointer;
      display: inline-block;
      height: 35px;
      padding: 0 24px;
      line-height: 35px;
      border: 1px solid #e6e6e6;
      text-decoration: none;
      border-radius: 20px;
      color: var(--color-base, #222);
      background-color: #fff;
    }
  }
}
